import * as PIXI from 'pixi.js';
import { noop as _noop } from 'lodash/util';
import Stage from './Stage';

const BACKGROUND_COLOR = 0x64b0ff;

class Game {
        /**
         * Game Constructor
         * @returns {Game}
         */
        constructor() {
                this.spritesheet = 'assets/spritesheetcollection.json';
                
                // Create renderer directly for PixiJS v8
                this.renderer = new PIXI.WebGLRenderer({
                        width: window.innerWidth,
                        height: window.innerHeight,
                        backgroundColor: BACKGROUND_COLOR,
                        antialias: true,
                        resolution: window.devicePixelRatio || 1
                });
                
                this.stage = new PIXI.Container();
                this.ticker = new PIXI.Ticker();

                return this;
        }

        async load() {
                // Load assets individually using PixiJS v8 Assets API
                await PIXI.Assets.load(this.spritesheet);
                
                this.onLoad();
        }

        onLoad() {
                document.body.appendChild(this.renderer.view);

                this.timer = 0;

                this.gameStage = new Stage({
                        spritesheet: this.spritesheet
                });

                this.stage.addChild(this.gameStage);

                this.resizeWindow();
                this.bindEvents();
                
                // Use the ticker for the update loop
                this.ticker.add(this.update.bind(this));
                this.ticker.start();
        }

        bindEvents() {
                window.addEventListener('resize', this.resizeWindow.bind(this));
        }

        resizeWindow() {
                this.renderer.resize(window.innerWidth, window.innerHeight);
        }

        update(deltaTime) {
                // Render the stage
                this.renderer.render(this.stage);
                
                // deltaTime is provided by the ticker
                this.timer += deltaTime * 16; // Convert to milliseconds

                this.gameStage.animate();
                this.gameStage.tickTask2Timer();

                if (this.timer >= 1000) {
                        this.timer = 0;

                        this.gameStage.addSpriteToReversedArray();
                }
        }
}

export default Game;